.loader {
  width: 96px;
  height: 96px;
  box-sizing: border-box;
  border: 10px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

